import { Component, ChangeDetectionStrategy, ChangeDetectorRef, NgZone } from "@angular/core";

import { App, NavController, Platform, ModalController } from "ionic-angular";

import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";
import { MarsAuthService } from "@services/auth.service";
import { Backend } from "@backend/index";
import { PaginationInstance } from "ngx-pagination";
import { MarsGeolocationService } from "@services/geolocation/geolocation.service";
import { MarsPictureService } from "@services/picture.service";
import { MarsAddressAutocompleteDirective } from "@directives/mars-address-autocomplete";


@Component({
    selector: "page-adopt",
    templateUrl: "adopt.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class AdoptPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;
    location: any;
    pets: Array<any>;
    token: string;
    pagination: PaginationInstance = {
        itemsPerPage: 4,
        currentPage: 1
    };

    constructor(public platform: Platform,
        public app: App,
        public navCtrl: NavController,
        public modalCtrl: ModalController,
        public changeDetector: ChangeDetectorRef,
        public locales: AppLocales,
        public zone: NgZone,
        public globals: AppGlobals,
        public authService: MarsAuthService,
        public geolocationService: MarsGeolocationService,
        public interactionService: MarsInteractionService,
        public pictureService: MarsPictureService) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    async ionViewDidLoad() {
        await this.getLocation();
        this.getPets();
        if (MarsAuthService.isLoggedIn()) this.token = MarsAuthService.getMarsToken();
        this.changeDetector.detectChanges();
    };

    ionViewWillEnter() { };

    async getLocation() {
        return new Promise((resolve, reject) => {
            try {
                MarsGeolocationService.onLoad(async () => {
                    let location = await this.geolocationService.getUserLocation({}) as any;
                    this.globals.currentAddress.location = { latitude: location.lat(), longitude: location.lng() };
                    resolve();
                });
            } catch (e) {
                this.interactionService.alert(this.translations.server_failure);
                this.pets = [];
            }
        });
    };

    onAddressSelected(address) {
        address.complement = this.globals.currentAddress.complement || "";
        if (!address.streetNumber) return this.interactionService.alert(this.translations.whoops_the_address_must_contain_the_street_number);;
        this.globals.currentAddress = address;
        this.changeDetector.detectChanges();
        this.refreshPets(0);
    }

    async refreshPets(currentPage) {
        this.pagination.currentPage = currentPage;
        delete this.pets;
        this.getPets();
    }

    getPetPicture(pet) {
        let picture = pet && pet.pictures ? this.pictureService.getPicture(pet.pictures[pet.pictures.length - 1]) : "";
        return `background-image: url(${picture});`;
    }

    async getPets() {
        try {
            let data = (await Backend.getPetsByStatus({
                pageSize: this.pagination.itemsPerPage,
                page: this.pagination.currentPage - 1,
                latitude: this.globals.currentAddress.location.latitude,
                longitude: this.globals.currentAddress.location.longitude,
                status: "adoptable"
            })).data;
            this.pets = data.pets;
        } catch (e) {
            
            this.interactionService.alert(this.translations.server_failure);
            this.pets = [];
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    viewPet(pet) {
        let viewModel = this.modalCtrl.create("PetDetailsPage", { petId: pet._id });
        viewModel.present();
    }

    async openPetModal(editablePet?) {
        let creationModal = this.modalCtrl.create("PetInformationPage", editablePet);
        creationModal.present();
        creationModal.onWillDismiss((pet) => {
            if (pet) {
                pet.status = "adoptable";
                this.savePet(pet);
            }
        });
    }

    async savePet(pet) {
        let spinner = this.interactionService.spinner({ content: this.translations.loading + "..." });
        try {
            let saved = (await Backend.savePet({ xAccessToken: this.token, pet: pet, status: pet.status })).data;
            this.refreshPets(this.pagination.currentPage);
        } catch (e) {
            
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            spinner.dismiss();
            this.changeDetector.detectChanges();
        }
    }
}


